.select-bar {
  margin-top: 15px;
  margin-left: 15px;
  width: 250px;
  border-radius: 10px;
}

.ant-select-selector {
  background: #f7f8f8;
}
.select-bar-category {
  color: #000 !important;
  width: 30% !important;
  background-color: #ffe64d !important;
}

.select-bar-search {
  width: 70% !important;
}

.select-bar-search > .ant-select-arrow {
  display: none;
}
