.board-container {
  height: calc(90vh - 40px);
}
.tile img {
  height: 100%;
}

.tile {
  width: calc((90vh - 40px) / 3);
  height: calc((90vh - 40px) / 3);
}

.side-board {
  height: calc(90vh - 40px);
}

.delete-box {
  height: 15vh;
  border: 1px solid grey;
  width: 15vh;
  background-color: #dededc;
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete-icon svg {
  height: 13vh;
  width: 13vh;
  color: grey;
}

.status-box {
  color: white;
  height: calc(75vh - 40px);
  width: 40vh;
}
