.tag {
  background-color: #d5e35ae5;
  height: 13%;
  color: white;
  border-radius: 20px;
  border: 1px black solid;
}

.tag-content {
  position: absolute;
  left: 25%;
  display: flex;
  flex-direction: row;
  margin-top: 13px;
  width: 50%;
}
.tag-content-badge {
  background-color: #dededc;
  color: white;
  height: 25px;
  border-radius: 5px;

  width: 25px;
  text-align: center;
}

.tag-content-name {
  margin-left: calc((100%-25px) * 0.2);
  height: 50%;
}
