.box {
  top: 30px;
  margin-left: 15px;
  padding: 0;
  position: relative;
  width: 250px;
  perspective: 1000px;
  transition: all 0.5s ease-in-out 0.5s;
}

.box:hover {
  box-shadow: 1px 2px 10px 5px solid #000;
}

.box .box-img {
  transform: rotateY(0);
  transition: all 0.5s ease-in-out 0;
  border-radius: 10px;
  border: 1px solid white;
  opacity: 1;
}

.box:hover .box-img {
  opacity: 0;
  transform: rotateY(-90deg);
}
.box .box-img .img-frame {
  width: 248px;
  height: 378px;
  border-radius: 10px;
}

.box .box-img .img-doll {
  position: absolute;
  top: 50px;
  left: 0px;
  width: 250px;
  height: 250px;
}

.box .box-img .id {
  text-align: left;
  letter-spacing: 0.5px;
  position: absolute;
  color: #8a8a8a;
  font-size: 24px;
  bottom: 4px;
  right: 10px;
  height: 30px;
  width: 50px;
}
.box .content {
  opacity: 0;
  color: white;
  border: 1px solid white;
  background: rgb(64, 64, 64);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  padding: 35px 20px 90px 20px;
  box-sizing: border-box;
  text-align: center;
  border-radius: 10px;
  transform: rotateY(90deg);
  transition: all 0.5s ease-in-out 0s;
}

.box:hover .content {
  opacity: 1;
  transform: rotateY(0);
}

.box .content .content-table {
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  padding: 20px 0;
  margin: 0 25px;
}

.box .content .content-table div {
  margin: 3px 0;
}
.box .button {
  background: none;
  top: 63px;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  padding: 260px 50px;
  box-sizing: border-box;
  text-align: center;
  align-items: center;
  transform: rotateY(90deg);
  opacity: 0;
  transition: all 0.5s ease-in-out 0s;
}

.box:hover .button {
  transform: rotateY(0);
  opacity: 1;
}
.box .button li {
  list-style: none;
  padding: 15px 0;
}
.box .button a {
  color: white;
  text-decoration: none;
  border: 1px solid transparent;
  padding: 5px 10px;
  background: #f0b001;
  border-radius: 10px;
}

.box .button a:hover {
  background: #e6b52f;
}
