.side-nav {
  flex: none !important;
  background: #232227 !important ;
  min-width: 10vh !important;
  width: 10vh !important;
}

.side-nav .ant-layout-sider-children {
  display: flex;
  flex-direction: column;
}

#active svg {
  color: #ffe64d;
}

.side-nav-icon {
  align-self: center;
  width: 6vh;
  height: 6vh;
  margin-top: 3vh;
  margin-bottom: 3vh;
}

.side-nav-icon svg {
  width: 6vh;
  height: 6vh;
  color: #3c3b3f;
  transition-timing-function: ease-in;
  transition-delay: 0.1s;
}

.side-nav-icon:hover svg {
  animation: flicker 3s linear infinite;
}

@keyframes flicker {
  0% {
    color: #ffe64d;
    opacity: 0.1;
  }
  2% {
    opacity: 1;
    color: #ffe64d;
  }
  8% {
    opacity: 0.1;
    color: #ffe64d;
  }
  9% {
    opacity: 1;
    color: #ffe64d;
  }
  12% {
    opacity: 0.1;
    color: #ffe64d;
  }
  20% {
    opacity: 1;
    color: #ffe64d;
  }
  25% {
    opacity: 0.3;
    color: #ffe64d;
  }
  30% {
    opacity: 1;
    color: #ffe64d;
  }

  70% {
    opacity: 0.7;
    color: #ffe64d;
  }

  72% {
    opacity: 0.2;
    color: #ffe64d;
  }

  77% {
    opacity: 0.9;
    color: #ffe64d;
  }
  100% {
    opacity: 0.9;
    color: #ffe64d;
  }
}
